<template>
  <div class="card">
    <ul :class="{'disable':true,'error':checkErrorClass(),'ok': submit.status }">
      <li v-for="(err, index) in errors" :key="`error_id_${index}`">
        {{ err }}
      </li>
    </ul>


    <ul>
        <li class="half">ID</li>
        <li class="half"></li>
        <li class="half"><input type="number" name="id" v-model="card_id" placeholder="id"></li>
        <li class="half"></li>

        <li class="half">Date</li>
        <li class="half">Ip</li>
        <li class="half"><input type="text" name="date" v-model="card_date" placeholder="yyyy-MM-dd HH:mm:ss.SSS"></li>
        <li class="half"><input type="text" name="ip" v-model="card_ip" placeholder="Ipv4"></li>

        <li class="half">Request</li>
        <li class="half">Status</li>
        <li class="half"><input type="text" name="request" v-model="card_request" placeholder="Http request type"></li>
        <li class="half"><input type="number" name="status" v-model="card_status" placeholder="Http Code"></li>

        <li>User Agent</li>
        <li><input type="text" name="user_agent" v-model="card_user_agent" placeholder="User Agent"></li>

        <li class="send">
          <input type="button" value="Delete" class="submitLog delete" v-on:click="deleteLog">
          <input type="submit" value="Save" class="submitLog save" v-on:click="checkForm">
        </li>

    </ul>


  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'LogCard',
  
  props: {
    log: {
      type: Object,
      default: () => (
         {
          id: null,
          date: null,
          ip: null,
          request: null,
          status: null,
          user_agent: null
         }
      )
    }
  },
  data(){
    return{
        errors:[],
         submit:{
          status: false
        },
        dateChecker: null,
        ipChecker: null,

        card_id:null,
        card_date: null,
        card_ip: null,
        card_request: null,
        card_status: null,
        card_user_agent: null

    }
  },
  methods:{
    checkErrorClass: function(){
        return this.errors > 0;
    },

    checkForm: function (e) {
      this.errors = [];
      this.submit.status = false;


      if(this.card_id == null){
        this.errors.push("The ID field cannot be empty")
        this.card_id = null;
      }
      
      if(!this.dateChecker.test(this.card_date)){
        this.errors.push("The Date field must follows yyyy-MM-dd HH:mm:ss.SSS format")
        this.card_date = null;
      }

      if(!this.ipChecker.test(this.card_ip)){
        this.errors.push("The IP field must follows IPv4 format")
        this.card_ip = null;
      }

      if(this.card_user_agent != null){
        if(this.card_user_agent.length >= 255){
          this.errors.push("The user_agent must smaller than 255 characters")
          this.card_user_agent = null;
        }
      }

      if(this.errors.length == 0){
        this.save();
      }else{
        this.displayerrors = true;
      }

      e.preventDefault();
    },

    getFilterModel: function(){
      return {
          "id": this.card_id,
          "date": this.card_date,
          "ip": this.card_ip,
          "request": this.card_request,
          "status": this.card_status,
          "user_agent": this.card_user_agent
      }
    },

    save: function(){
      this.errors = [];
      this.logs = [];
      this.submit.status = false;

      let headers = new Headers({
          "Content-Type":	"application/json"
      });
      
      fetch(this.getLogService, {
                    method: 'PUT',
                    headers: headers,
                    body: JSON.stringify(this.getFilterModel())
                }).then((response) => {
                    console.log(response);

                    if(response.status == 200){
                      this.submit.status = true;
                      this.errors.push("Log has been saved");
                    }else{
                        this.submit.status = false;
                    }

                    return response.json();
                }).then((data) => {

                    if('error_list' in data){
                      this.errors.push(data.error_list[0].message)
                    }
                    console.log(data);
                })
                .catch((error) => {
                    //this.errors.push(error);
                    console.error(error);
                });
    },

    deleteLog: function(){
      this.errors = [];
      this.logs = [];
      this.submit.status = false;

      if(this.card_id != null){

        let headers = new Headers({
            "Content-Type":	"application/json"
        });
        
        fetch(this.getLogService+`/${this.card_id}`, {
                      method: 'DELETE',
                      headers: headers,
                      body: null
                  }).then((response) => {
                      console.log(response);

                      if(response.status == 200){
                        this.submit.status = true;
                        this.errors.push("Log has been deleted");
                      }else{
                          this.submit.status = false;
                      }

                      return response.json();
                  }).then((data) => {

                      if('error_list' in data){
                        this.errors.push(data.error_list[0].message)
                      }
                      console.log(data);
                  })
                  .catch((error) => {
                      //this.errors.push(error);
                      console.error(error);
                  });
      }else{
        this.errors.push("ID cannot be empty");
      }
    }
    
  },
  computed: {

    ...mapGetters([
      'getLogService',
      'getRegexIp',
      'getRegexDate'

    ])
  },
  mounted(){
        this.card_id= this.log.id;
        this.card_date= this.log.date;
        this.card_ip= this.log.ip;
        this.card_request= this.log.request;
        this.card_status= this.log.status;
        this.card_user_agent= this.log.user_agent;

        this.ipChecker = this.getRegexIp;
        this.dateChecker = this.getRegexDate;
        console.log(this.log)
  }
}
</script>

