<template>
  <div class="formLog">
    <ul :class="{'disable':true,'error':checkErrorClass(),'ok': submit.status }">
      <li v-for="(err, index) in errors" :key="`error_id_${index}`">
        {{ err }}
      </li>
    </ul>

    <form id="logManualInsert"  v-on:submit.prevent="checkForm" method="get"> 
    <ul>
        <li class="half">Start Date</li>
        <li class="half">End Date</li>
        <li class="half"><input type="Date" name="start_date" v-model="startDate" placeholder="yyyy-MM-dd"></li>
        <li class="half"><input type="Date" name="end_date" v-model="endDate" placeholder="yyyy-MM-dd"></li>

        <li class="half">IP</li>
        <li class="half"></li>
        <li class="half"><input type="text" name="ip" v-model="ip" placeholder="IPv4"></li>

        <li class="send"><input type="submit" value="Search" class="submitLog"></li>

    </ul>
    </form>

    <LogList :logs="logs"></LogList>

  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import LogList from './LogList.vue'

export default {
  name: 'SearchFilter',
  components: {
    LogList
  },
  props: {
    msg: String
  },
  data(){
    return{
        logs:[],
        dateChecker: null,
        ipChecker: null,

        errors:[],
        submit:{
          status: false
        },

        startDate: null,
        endDate: null,
        ip: null
    }
  },
  methods:{
    
    checkErrorClass: function(){
        return this.errors.length > 0;
    },

    checkForm: function (e) {
      this.errors = [];
      this.submit.status = false;

      let model = this.getFilterModel();
      
      if(model.start_date != null){
        if(!this.dateChecker.test(model.start_date)){
          this.errors.push("Pick a Start Date")
        }
      }

      if(model.end_date != null){
        if(!this.dateChecker.test(model.end_date)){
          this.errors.push("Pick a End Date")
        }
      }

      if(model.ip == null){
        if(!this.ipChecker.test(model.ip)){
          this.errors.push("The IP field must follows IPv4 format")
        }
      }

      if(this.errors.length == 0){
        this.submitToApi(model);
      }

      e.preventDefault();
    },

    getFilterModel: function(){
      return {
          "start_date": this.startDate+" 00:00:00.000",
          "end_date": this.endDate+" 00:00:00.000",
          "ip": this.ip
      }
    },

    submitToApi: function(model){
      this.errors = [];
      this.logs = [];
      this.submit.status = false;

      console.log(model)


      fetch(this.getLogService+`search/basic/?start_date=${model.start_date}&end_date=${model.end_date}&ip=${model.ip}`, {
                    method: 'GET',
                    headers: new Headers({}),
                    body: null
                }).then((response) => {
                    return response.json();
                }).then((data) => {
                    if('error_list' in data){
                      this.errors.push(data.error_list[0].message)
                    }
                    console.log(data);
                    this.logs = data;
                })
                .catch((error) => {
                    this.errors.push(error);
                    console.error(error);
                });
    }
  },
  computed: {

    ...mapGetters([
      'getLogService',
      'getRegexIp',
      'getRegexDate'

    ])
  },
  mounted(){
    this.ipChecker = this.getRegexIp;
    this.dateChecker = this.getRegexDate;
  }
}
</script>

